import { Component, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Observable } from 'rxjs';
import { UserdataProvider } from '../../providers/userdata/userdata';

import { ProductsProvider } from '../../providers/products/products';

@IonicPage()
@Component({
  selector: 'page-product-detail',
  templateUrl: 'product-detail.html',
})
export class ProductDetailPage {
  @ViewChild('addToBtn')
  private addBtn: ElementRef;
  variantColor: any = [];
  variantSize: any = [];
  selectedColor: string;
  selectedSize: string;
  requiredData: any = {
    selectedColor: '',
    selectedSize: ''
  }
  variantProducts: any = [];
  itemno_increase: number = 0;
  cartno: number;
  cart: Observable<boolean>;
  productId: any;
  productDetails: any;
  addToCartBtn: string = 'add to bag';

  constructor(public navCtrl: NavController, public navParams: NavParams, private productsProvider: ProductsProvider,
    public userdataprovider: UserdataProvider) {
    // this.productId = this.navParams.get("productDetails");
    // Single Product Product ID with no Variants.
    // this.productId = '13122352';
    // Single Product Product ID with Variants in Color and Size.
    // this.productId = `13122338`;
    // Multiple Color Multiple Size.
    this.productId = '13122340';
    console.log(this.productId, 'ionViewDidLoad ProductDetailPage');
    if (this.productId) {
      this.productsProvider.fetchProductDetails(this.productId).subscribe(productDetails => {
        console.log(productDetails['Product'], '=====================Product Details From Details=====================');
        this.productDetails = productDetails['Product'];
        this.variantProducts = this.productDetails.VariantProducts;
        for (let i = 0; i < this.productDetails.VariantProperties.length; i++) {
          if (this.productDetails.VariantProperties[i].PropertyName == 'Color') {
            this.variantColor = this.productDetails.VariantProperties[i].VariantValues;
            console.log(this.variantColor, '================Variant Color==============================')
          }
        }
        for (let i = 0; i < this.productDetails.VariantProperties.length; i++) {
          if (this.productDetails.VariantProperties[i].PropertyName == 'Size') {
            this.variantSize = this.productDetails.VariantProperties[i].VariantValues;
            console.log(this.variantSize, '================Variant Color==============================')
          }
        }
      });
    }
    // this.productsProvider.getCartItemQuantity();
  }

  addToCart(product) {
    console.log(product)
    if (localStorage.getItem("token")) {
      console.log(this.requiredData,'=======================Required Data=================');
      this.getVariantProductId();
      // this.productsProvider.validateToken(localStorage.getItem("token")).subscribe(data => {
      //   console.log(data, '===========================Validate Token Data==========================')
      //   this.productsProvider.addCartItems(this.productId, localStorage.getItem("token"), 0).subscribe(cartStatus => {

      //     console.log(cartStatus, '===========================Cart Status Data==========================')
      //     this.addToCartBtn = 'Added';
      //     console.log(this.addBtn, "color")
      //     //this.addBtn.ElementRef.nativeElement.style.backgroundColor ='red';
      //   });
      // });
    }
  }

  getSelectedColor(color) {
    console.log("getSelected Color", color);
    this.requiredData.selectedColor = color;
    this.getVariantProductByColor()
  }

  getSelectedSize(size) {
    console.log("getSelected Color", size);
    this.requiredData.selectedSize = size;
    this.getVariantProductBySize();
  }

  goTo(page) {
    this.navCtrl.setRoot(page);
  }

  canActivateAddCart() {
    if (this.variantColor || this.variantSize) {
      if (this.variantColor.length == 0 && this.variantSize.length == 0) {
        return false;
      } else if (this.variantColor.length > 0 && this.requiredData.selectedColor && this.variantSize.length > 0 && this.requiredData.selectedSize) {
        return false;
      } else if (this.variantColor.length == 0 && this.variantSize.length > 0 && this.requiredData.selectedSize) {
        return false;
      } else if (this.variantColor.length > 0 && this.requiredData.selectedColor && this.variantSize.length == 0) {
        return false;
      } else {
        return true;
      }
    } else {
      return false;
    }
  }


  getVariantProductId(){

    if(this.variantProducts){
      let variantProductColor = this.variantProducts.filter(variants => {
        return  variants.VariantProperties.find(prop=>prop.PropertyName =="Color" && prop.propertyvalue.toLowerCase() == this.requiredData.selectedColor)
        })

        console.log(variantProductColor);
        if(variantProductColor.length > 0){
            let variantProductId = variantProductColor.find(variants => {
              return  variants.VariantProperties.find(prop=>prop.PropertyName =="Size" && prop.propertyvalue.toLowerCase() == this.requiredData.selectedSize)
              });
              console.log(variantProductId,"variant product data0")
        }
       
     
     
    }else{

    }
      

  }

  getVariantProductByColor(){
    let variantProductColor = this.variantProducts.filter(variants => {
      return  variants.VariantProperties.find(prop=>prop.PropertyName =="Color" && prop.propertyvalue.toLowerCase() == this.requiredData.selectedColor)
      });
      console.log(variantProductColor,"variant color");

  }

  getVariantProductBySize(){
    let variantProductSize = this.variantProducts.find(variants => {
      return  variants.VariantProperties.find(prop=>prop.PropertyName =="Size" && prop.propertyvalue.toLowerCase() == this.requiredData.selectedSize)
      });

      console.log(variantProductSize,"variant size");
  }
}



