import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { CartProvider } from '../../../providers/cart/cart';
import { Observable } from 'rxjs';

/**
 * Generated class for the InternalBadgeComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'internal-badge',
  templateUrl: 'internal-badge.html'
})
export class InternalBadgeComponent {

  text: string;
  ProductList:any;
  public items_added: number = 0;
  cart: Observable<boolean>;
  cartquantity: any;

  constructor(private navCtrl: NavController,public cartProvider :CartProvider) {
    console.log('Hello InternalBadgeComponent Component');
    this.text = 'Hello World';
    this.addToBag();
  }

  gotoMybag(){
    this.navCtrl.setRoot('MyBagPage');
    this.ProductList = 'MyBag';  
  }

  ionViewDidLoad(){
    this.addToBag();
  }

  addToBag(){
    this.cartProvider.getCartItemQuantity();
   this.cart = this.cartProvider.isCartItem();
   this.cart.subscribe(cartquantity =>{
     if(cartquantity){
       console.log(this.cartProvider.getCartItemQuantity(),"geting cart::::::::::::::")
      this.cartquantity = this.cartProvider.getCartItemQuantity();
     }
     else{
       console.log("Cart is Empty")
     }
   })
   
  }
}



